//ABSTRACTS
@import 'abstracts/variables';
@import 'abstracts/brand-settings';
@import 'abstracts/mixins';
@import 'abstracts/app-shell-source';
@import 'vendors/bootstrap-custom-base';

// VIEWPORT OBJECTS
@import "../../node_modules/bootstrap/scss/reboot";
@import 'base/icons';
@include button-appshell;
@include d-elements-appshell;
@include qty-badge-appshell;
@include header-appshell;

#main-menu,
#header-search,
#notifiers{
    display: none;
}
body{
    overflow: hidden;
    color: $c-text-dark;
}

// Bootstrap Grids for Header
.container {
    max-width: calc(1160px + 32px);
    padding-right: $gutter !important;
    padding-left: $gutter !important;
    margin-right: auto;
    margin-left: auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: $gutter/2;
    margin-left: $gutter/2;
}

.align-items {
    &-center {
        align-items: center !important;
    }
}

.d{
 &-block {
    display: block;
 }
 &-none {
    display: none;
 }
}

#ash-plc{
    height: calc(100vh - #{$header-height-mobile});
    width: 100%;
    position: fixed;
    background: $c-grey-light;
    z-index: $z-normal;
    top: $header-height-mobile;
    left: 0;

    [class*="asp-"]{
        display: block;
        position: absolute;
        background: linear-gradient(to right, rgb(238, 238, 238) 8%, rgb(221, 221, 221) 18%, rgb(238, 238, 238) 33%) 0% 0% / 1000% 100%;
        animation: 1s linear 0s infinite normal forwards running ash-plc;
    }
}

@keyframes ash-plc{
    0% {
        background-position: 50% 0px;
    }
    100% {
      background-position: -50% 0;
    }
}

.page-home{
    .asp{
        &-1{
            width: 90%;
            height: 40%;
            bottom: 10%;
            left: 5%;
        }
        &-2{
            width: 60%;
            height: 5%;
            top: 10%;
            left: 20%;
        }
        &-3{
            width: 60%;
            height: 5%;
            top: 17%;
            left: 20%;
        }
        &-4{
            width: 10%;
            height: 5%;
            top: 30%;
            left: 45%;
        }
    }
}

.page-product-list{
    .asp{
        &-1{
            width: 90%;
            height: 5%;
            top: 5%;
            left: 5%;
        }
        &-2, &-3{
            width: 90%;
            height: 40%;
            top: 15%;
            left: 5%;
        }
        &-3{
            top: 57%;
        }
    }
}

.page-product-detail{
    .asp{
        &-1{
            width: 90%;
            height: 50%;
            top: 5%;
            left: 5%;
        }
        &-2, &-3, &-4, &-5{
            width: 90%;
            height: 5%;
            left: 5%;
            top: 65%;
        }
        &-3{
            top: 73%;
        }
        &-4{
            top: 81%;
        }
        &-5{
            top: 89%;
        }
    }
}

// DESKTOP
@media (min-width: $breakpoint3) {
    @include header-appshell-desktop;
    @include m-elements-appshell-desktop;

    // Bootstrap Grids for Header Desktop
    .col{
        &-lg-2{
            flex: 0 0 16.66667%;
            max-width: 16.66667%;
        }
        &-lg-5{
            flex: 0 0 41.66667%;
            max-width: 41.66667%;
        }
    }

    #ash-plc{
        height: calc(100vh - #{$header-height-desktop});
        top: $header-height-desktop;
    }

    .page-product-list{
        .asp{
            &-1{
                width: 90%;
                height: 5%;
                top: 10%;
                left: 5%;
            }
            &-2, &-3, &-4, &-5{
                width: 20.25%;
                height: 30%;
                top: 20%;
                left: 5%;
            }
            &-3{
                left: 28.25%;
            }
            &-4{
                left: 51.50%;
            }
            &-5{
                left: 74.75%;
            }
        }
    }

    .page-product-detail{
        .asp{
            &-1{
                width: 50%;
                height: 90%;
                top: 5%;
                left: 5%;
            }
            &-2, &-3, &-4, &-5{
                width: 35%;
                height: 5%;
                left: auto;
                right: 5%;
                top: 25%;
            }
            &-3{
                top: 33%;
            }
            &-4{
                top: 41%;
            }
            &-5{
                top: 49%;
            }
        }
    }
}

@each $i, $clr in $c-banner{
    .bnr-bg-#{$i} {
        @each $p, $v in $clr{
            #{$p}: $v;
        }       
    }
}

h1, h2, h3, h4, h5, h6 {
    font-family: $font2;
    font-weight: 600;
}

h1 {
    font-size: 22px;
}