@mixin button-appshell {
    button{
        padding: 0;
        background: none;
        border: none;
        color: inherit;
        text-align: left;
    
        &:focus{
            outline: none;
        }
    }
}

@mixin d-elements-appshell {
    // desktop elements
    .d {
        display: none;
    }
    .only-m {
        display: block !important;
    }
    .only-m-f {
        display: flex !important;
    }

    .only-d-f,
    .only-d {
        display: none !important;
    }
}

@mixin qty-badge-appshell {
    .qty-badge {
        background: $c-primary;
        color: $c-text-light;
        border-radius: 50%;
        width: 22px;
        line-height: 22px;
        font-size: 10px;
        text-align: center;
        &[data-qty="0"]{
            display: none;
        }
    }
}

@mixin header-appshell {
    // top header
    .header-top {
        position: relative;
        width: 100%;
        background: $header-top-bg;
        color: $header-top-color;
        transition: all .5s ease;
        height: 68px;
        display: flex;
        align-items: center;
        > .container {
            .row {
                align-items: center;
            }
        }
    }

    #nav-user-menu {
        ul {
            display: flex;
            margin: 0 -10px;
            li {
                position: relative;
                padding: 0 10px;
                > *:not(#cart-notifier) {
                    width: 28px;
                    height: 28px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }

    .btn-user {
        svg {
            width: 15px;
            height: 20px;
        }
    }

    .btn-search {
        svg {
            width: 18px;
            height: 18px;
        }
    }

    .btn-cart{
        position: relative;
        svg {
            width: 25px;
            height: 20px;
        }
        .qty-badge {
            position: absolute;
            top: 0;
            right: -6px;
            width: 16px;
            height: 16px;
            line-height: 16px;
        }
    }

    .btn-menu {
        svg {
            width: 16px;
            height: 14px;
        }
    }

    .notice-cmp {
        border-radius: 5px;
        background-color: #FB1415;
        margin-bottom: 10px;
        .content {
            padding: 10px 12px;
            display: flex;
            align-items: center;
            text-align: center;
            color: #fff;
            font-size: 12px;
            > span {
                display: none;
            }

            .item {
                @include line-clamp(2);
                height: 32px;
                line-height: 16px;
            }

            .btn {
                display: none;
            }
        }

        .swiper-container {
            height: 32px;
        }
    }
}


// DESKTOP APPSHELL MIXINS
@mixin m-elements-appshell-desktop {
    // mobile elements
    .m {
        display: none !important;
    }
    .only-d {
        display: block !important;
    }
    .only-d-f {
        display: flex !important;
    }
    .only-m-f,
    .only-m {
        display: none !important;
    }
}

@mixin header-appshell-desktop {
    .header-top {
        height: auto;
        display: block;
        > .container {
            .row {
                padding: 36px 0;
            }
        }
    }

    #nav-main-menu {
        ul {
            display: flex;
            justify-content: center;
            li {
                > * {
                    position: relative;
                    color: $c-text-dark;
                    font-size: 14px;
                    padding: 0 2.5px;

                    .qty-badge {
                        position: absolute;
                        right: -20px;
                        top: -15px;
                        text-decoration: none;
                    }

                    svg {
                        width: 12px;
                        height: 10px;
                    }
                }
                margin-right: 40px;
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    .notice-cmp {
        margin-bottom: 20px;
        .content {
            padding: 10px 20px;
            font-size: 14px;
            > span {
                display: block;
                margin-right: 10px;
                padding-right: 10px;
                position: relative;
                &:after {
                    content: '';
                    height: 20px;
                    width: 1px;
                    background-color: #fff;
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);

                }
            }

            .item {
                padding: 0 20px;
                @include line-clamp(1);
                height: 20px;
                line-height: 20px;
            }

            .btn {
                padding: 0 50px;
                display: flex;
            }

            .swiper-container {
                height: 20px;
            }
        }
    }
}